<template>
    <div class="">
        <span class="name">:</span>
        <m-select :list="provinceList" title="" 
                    :value="province" :showWrapperActive="provinceActive"
                    @change_active="changeProvinceActive"
                    @change="changeProvince" className="province"/>
        <m-select :list="cityList" title="" :value="city" 
                    :showWrapperActive="cityActive" className="city"
                    @change_active="changeCityActive"
                    @change="changeCity" :disabled="cityDisabled"/>
        <span>:</span>
        <el-select
            v-model="searchWord"
            filterable
            remote
            reserve-keyword
            placeholder=""
            :remote-method="remoteMethod"
            :loading="loading">
            <el-option
                v-for="item in searchList"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
        </el-select>

    </div>

</template>

<script>
import MSelect from './select.vue'
import api from '@/api/index.js'
export default {
    data () {
        return {
            provinceList: [],
            province: "",
            cityList: [],
            city: "",
            provinceActive: false,
            cityActive: false,
            searchList: ["", "", "", "", ""],
            searchWord: '',
            loading: false,
            cityDisabled: true
        }
    },
    created () {
        api.getProvinceList().then(res => {
            // console.log(res)
            this.provinceList = res.data.data.map((item) => {
                item.name = item.provinceName
                return item
            })
        });
    },
    components: {
        MSelect
    },
    methods: {
        changeProvinceActive(flag) {
            this.provinceActive = flag;
            if (flag) {
                this.cityActive = false;
            }
        },
        changeCityActive(flag) {
            this.cityActive = flag;
            if (flag) {
                this.provinceActive = false;
            }
        },
        changeProvince(item) {
            this.province = item.name;
            this.cityDisabled = false;
            this.cityList = item.cityInfoList
        },
        changeCity(item) {
            this.city = item.name;
            this.$store.dispatch('setPosition', item)
            this.$router.push({name: 'index'})
        },
        remoteMethod() {
            // 
        }
    }
}
</script>

<style lang="scss">
    @import "@/assets/css/changecity/iselect.scss"
</style>


